*
{
  margin:0;
  padding:0;
}
body {
  background: url("http://superdevresources.com/wp-content/uploads/2014/02/pattern-background.png");
  font-family: "Bree Serif", serif;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}
h1
{
  text-align: center;
  color: #f71aa2;
}
.app {
  background: linear-gradient(5deg, #ff961d 0%, #f71aa2 100%);
  width: 95%;
  min-height: 200px;
   border-radius: 15px;
  padding: 20px;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
  display: flex;
  justify-content: space-evenly;
}
.Question-Section {
  width: 100%;
  position: relative;
}
.Question-count {
  margin-bottom: 20px;
}

.Question-text {
  margin-bottom: 12px;
  color:white;
  font-size: 30px;
}
.Answer-Section{
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

}
button {
  width: 100%;
  font-size: 16px;
  color: #ffffff;
  background: linear-gradient( #ff961d 0%, #f71aa2 100%);
  border-radius: 15px;
  display: flex;
  padding: 5px;
  justify-content: flex-start;
  align-items: center;
  border: 3px solid #6d0ae6;
  cursor: pointer;
}

button:hover {
  color:blue;
}

button:focus {
  color:brown;
}
.score-card
{
  display: flex;
  align-items: center;
  justify-content: center;
  color:green;
  font-size: 30px;
  font-weight: bold;
}